import { Component, Input, OnInit} from '@angular/core';
import {Settings} from '../../models/settings';
import {SettingsService} from '../../services/settings.service';
import {CommunicationService} from '../../services/communication.service';
import {DevicesService} from '../../services/devices.service';
import {Device} from '../../models/device';
import {Router} from '@angular/router';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css'],
  providers: [DevicesService, SettingsService, CommunicationService]
})
export class SettingsComponent implements OnInit{
    
    @Input() setting: Settings[];
    @Input() devices = new Array<Device>();
    @Input() device: Device;  

    constructor(private communicationService: CommunicationService, private router: Router, private settingsService: SettingsService, private devicesServices: DevicesService) {
    this.communicationService = communicationService;
    this.settingsService = settingsService;
    this.devicesServices = devicesServices;
  }

  addSetting() {
    this.setting = this.settingsService.getSetting();
     this.settingsService.addSetting(this.setting);
  }

  removeSetting(){
    this.setting = this.settingsService.getSetting();
  }

 selectDevice(device){
    this.device = this.devicesServices.storeDevice(device);
    if (this.device!=undefined) {    
      alert("Dispositivo " +this.device.name+ " selecionado!");
    } else {
      alert("Não foi possível selecionar o dispositivo!");
    }
    this.router.navigate(['home']);
    return this.device;   
  }

  ngOnInit() {
    this.setting = this.settingsService.getSetting();
    console.log(this.setting.length);
    //this.devicesServices.descobertaDispositivo().subscribe(response => {
    this.communicationService.descobertaDispositivo().subscribe(response => {
      this.devices = response?response.json().devices.map(device=>{
        return new Device(device.id,device.ip,'8080',device.nome,device.mac);
      }):[];
      console.log(this.devices);
    }, erro => console.log(erro));    
  }

}



